import { useTranslation } from "gatsby-plugin-react-i18next";

import Card from "react-bootstrap/Card";
import { EditionData } from "../../data";

import useEventDates from "../../hooks/useEventDates";
import { CardData, cards } from "../general/cards";

import * as styles from "./conference.module.css";

type ConferenceCardProps = {
  card: CardData;
};

const ConferenceCard: React.FC<ConferenceCardProps> = ({ card }) => {
  return (
    <Card className={styles.card} text={"dark"}>
      <Card.Header className={`textMuted ${styles.cardHeader}`}>
        {card.time}
      </Card.Header>

      <Card.Body>
        <Card.Title className={styles.cardTitle}>{card.title}</Card.Title>
        <Card.Subtitle className={styles.cardSubtitle}>
          <Card.Link className={"textMuted"} href={card.contact}>
            {card.speaker}
          </Card.Link>
        </Card.Subtitle>
        <Card.Text className={"p"}>{card.details}</Card.Text>
      </Card.Body>
    </Card>
  );
};

type ConferenceProps = {
  editionData: EditionData;
};

const Conference: React.FC<ConferenceProps> = ({ editionData }) => {
  const { t } = useTranslation();

  const edition = editionData.edition;
  const { conference: date } = useEventDates(editionData);
  const details = t("details", { returnObjects: true }) as string[];

  return (
    <div className={`page ${styles.conference}`}>
      <h2 className={"date"}>{date}</h2>
      <h1 className={"title"}>{t("title")}</h1>
      {/*Conference list of cards*/}
      {edition !== 2023 && (
        <ul className={"ul"}>
          {cards[edition].map((item, index) => (
            <ConferenceCard key={"conferenceCard" + index} card={item} />
          ))}
        </ul>
      )}
      {/* Details for applying to the conference */}
      {edition === 2023 && (
        <div className={styles.conferenceDetails}>
          {details.map((item, index) => (
            <p key={"conferenceDetails" + index} className={"p"}>
              {item}
            </p>
          ))}
        </div>
      )}
      {/* Button for applying to present at the conference */}
      {edition === 2023 && (
        <div className={`button ${styles.button}`}>
          <a
            className={`buttonLink ${styles.buttonLink}`}
            target={"_blank"}
            rel={"noreferrer"}
            href={
              "https://forms.zohopublic.eu/nitrolanguageprocessing/form/nscriereNitroConferine/formperma/qP1qRf1y7vpsa6zMnNbX5GphnEd-HCc5VrY4fst-Ysw"
            }
          >
            {t("button")}
          </a>
        </div>
      )}
      {/* Link for joining the conference online */}
      {/* {edition === 2024 && (
        <a
          className={`buttonLink ${styles.buttonLink}`}
          target={"_blank"}
          rel={"noreferrer"}
          href={"#conference2024"}
        >
          {t("link")}
        </a>
      )} */}
      {/* The conference has ended */}
      {edition !== 2023 && <p className={`center p`}>{t("endDescription")}</p>}
    </div>
  );
};

export default Conference;
